.btn{
  display: flex;
  justify-content: center;
  align-items: center;
  background: $main;
  border-radius: 0.25rem;
  font-weight: 700;
  color: #fff;
  background: $primary;
  padding: 1rem 2rem;
  &.outline{
    background: none;
    color: $main;
    border: .0625rem solid #a3a3a3;
  }
}